import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './CreateBoard.css';

const templates = [
  { id: 1, name: 'Project Management', description: 'Manage your projects efficiently.' },
  { id: 2, name: 'Task Tracking', description: 'Track your tasks and to-dos.' },
  // Add more templates as needed, following Pipefy's examples
];

const CreateBoard = () => {
  const [boardName, setBoardName] = useState('');
  const [boardDescription, setBoardDescription] = useState('');
  const [selectedTemplate, setSelectedTemplate] = useState(null);
  const navigate = useNavigate();

  const handleCreateBoard = async () => {
    const newBoard = {
      name: boardName,
      description: boardDescription,
      template: selectedTemplate,
    };

    try {
      await axios.post('http://localhost:5000/api/boards', newBoard);
      navigate('/boards');
    } catch (error) {
      console.error('Error creating board:', error);
    }
  };

  return (
    <div className="create-board">
      <h1>Create New Board</h1>
      <div className="form-group">
        <label htmlFor="boardName">Board Name</label>
        <input
          type="text"
          id="boardName"
          value={boardName}
          onChange={(e) => setBoardName(e.target.value)}
        />
      </div>
      <div className="form-group">
        <label htmlFor="boardDescription">Board Description</label>
        <textarea
          id="boardDescription"
          value={boardDescription}
          onChange={(e) => setBoardDescription(e.target.value)}
        />
      </div>
      <div className="templates">
        <h2>Select a Template</h2>
        {templates.map((template) => (
          <div
            key={template.id}
            className={`template-item ${selectedTemplate === template.id ? 'selected' : ''}`}
            onClick={() => setSelectedTemplate(template.id)}
          >
            <h3>{template.name}</h3>
            <p>{template.description}</p>
          </div>
        ))}
        <div
          className={`template-item ${selectedTemplate === null ? 'selected' : ''}`}
          onClick={() => setSelectedTemplate(null)}
        >
          <h3>Blank Board</h3>
        </div>
      </div>
      <button onClick={handleCreateBoard}>Create Board</button>
    </div>
  );
};

export default CreateBoard;
