// src/actions/expenseActions.js
import { createAsyncThunk } from '@reduxjs/toolkit';

export const fetchExpenses = createAsyncThunk('expenses/fetchExpenses', async () => {
    const fakeData = [
        { id: 1, title: 'Groceries', amount: 50, date: '2024-06-01', category: 'Food', recurring: false },
        { id: 2, title: 'Electricity Bill', amount: 75, date: '2024-06-02', category: 'Utilities', recurring: true, recurrencePeriod: 'Monthly' },
        // Add more fake data as needed
    ];
    return fakeData;
});

export const addExpense = createAsyncThunk('expenses/addExpense', async (expense) => {
    const fakeExpense = { ...expense, id: new Date().getTime() };
    return fakeExpense;
});
