// src/components/ExpenseItem.js
import React from 'react';
import { Card, CardContent, Typography } from '@mui/material';

const ExpenseItem = ({ expense }) => {
    return (
        <Card>
            <CardContent>
                <Typography variant="h6">{expense.title}</Typography>
                <Typography variant="body1">${expense.amount}</Typography>
                <Typography variant="body2">{expense.date}</Typography>
                <Typography variant="body2">Category: {expense.category}</Typography>
                {expense.recurring && <Typography variant="body2">Recurring: {expense.recurrencePeriod}</Typography>}
            </CardContent>
        </Card>
    );
};

export default ExpenseItem;
