import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './dashboard.css'

const Dashboard = () => {
  const [expenses, setExpenses] = useState([]);
  const [income, setIncome] = useState([]);
  const [tasks, setTasks] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const expenseResponse = await axios.get('/data/expenses.json');
        const incomeResponse = await axios.get('/data/income.json');
        const taskResponse = await axios.get('/data/tasks.json');

        setExpenses(expenseResponse.data);
        setIncome(incomeResponse.data);
        setTasks(taskResponse.data);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, []);

  const calculateTotal = (items) => {
    return items.reduce((total, item) => total + parseFloat(item.amount), 0);
  };

  return (
    <div className="dashboard">
      <h1>Dashboard</h1>
      <div className="dashboard-section">
        <h2>Total Expenses</h2>
        <p>${calculateTotal(expenses).toFixed(2)}</p>
      </div>
      <div className="dashboard-section">
        <h2>Total Income</h2>
        <p>${calculateTotal(income).toFixed(2)}</p>
      </div>
      <div className="dashboard-section">
        <h2>Tasks</h2>
        <ul>
          {tasks.map(task => (
            <li key={task.id}>{task.title}</li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default Dashboard;
