// src/components/ExpenseForm.js
import React from 'react';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import { TextField, Button, MenuItem, Select, InputLabel, FormControl, Switch, FormControlLabel } from '@mui/material';
import { useDispatch } from 'react-redux';
import { addExpense } from '../../actions/expenseActions';

const ExpenseSchema = Yup.object().shape({
    title: Yup.string().required('Title is required'),
    amount: Yup.number().required('Amount is required').positive('Amount must be positive'),
    date: Yup.date().required('Date is required'),
    category: Yup.string().required('Category is required'),
    recurring: Yup.boolean(),
    recurrencePeriod: Yup.string().when('recurring', {
        is: true,
        then: Yup.string().required('Recurrence period is required')
    }),
});

const ExpenseForm = () => {
    const dispatch = useDispatch();

    return (
        <Formik
            initialValues={{ title: '', amount: '', date: '', category: '', recurring: false, recurrencePeriod: '' }}
            validationSchema={ExpenseSchema}
            onSubmit={(values, { resetForm }) => {
                dispatch(addExpense(values));
                resetForm();
            }}
        >
            {({ errors, touched, values }) => (
                <Form>
                    <Field
                        name="title"
                        as={TextField}
                        label="Title"
                        helperText={touched.title && errors.title}
                        error={touched.title && !!errors.title}
                    />
                    <Field
                        name="amount"
                        as={TextField}
                        label="Amount"
                        type="number"
                        helperText={touched.amount && errors.amount}
                        error={touched.amount && !!errors.amount}
                    />
                    <Field
                        name="date"
                        as={TextField}
                        label="Date"
                        type="date"
                        InputLabelProps={{ shrink: true }}
                        helperText={touched.date && errors.date}
                        error={touched.date && !!errors.date}
                    />
                    <FormControl>
                        <InputLabel>Category</InputLabel>
                        <Field
                            name="category"
                            as={Select}
                            error={touched.category && !!errors.category}
                        >
                            <MenuItem value="Food">Food</MenuItem>
                            <MenuItem value="Transport">Transport</MenuItem>
                            <MenuItem value="Utilities">Utilities</MenuItem>
                            <MenuItem value="Entertainment">Entertainment</MenuItem>
                        </Field>
                        {touched.category && errors.category && <div>{errors.category}</div>}
                    </FormControl>
                    <FormControlLabel
                        control={
                            <Field
                                name="recurring"
                                as={Switch}
                                color="primary"
                                type="checkbox"
                                checked={values.recurring}
                            />
                        }
                        label="Recurring"
                    />
                    {values.recurring && (
                        <FormControl>
                            <InputLabel>Recurrence Period</InputLabel>
                            <Field
                                name="recurrencePeriod"
                                as={Select}
                                error={touched.recurrencePeriod && !!errors.recurrencePeriod}
                            >
                                <MenuItem value="Daily">Daily</MenuItem>
                                <MenuItem value="Weekly">Weekly</MenuItem>
                                <MenuItem value="Monthly">Monthly</MenuItem>
                            </Field>
                            {touched.recurrencePeriod && errors.recurrencePeriod && <div>{errors.recurrencePeriod}</div>}
                        </FormControl>
                    )}
                    <Button type="submit" variant="contained">Add Expense</Button>
                </Form>
            )}
        </Formik>
    );
};

export default ExpenseForm;
