import React, { useState, useRef, useEffect } from 'react';
import { DndProvider, useDrag, useDrop } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { Box, Card, CardContent, Typography, Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import './BoardPage.css';

const ItemTypes = {
  CARD: 'card',
};

const fakeBoardData = {
  name: 'Recruitment',
  description: 'Manage your recruitment process efficiently',
  stages: [
    {
      id: 1,
      name: 'To Do',
      tasks: [
        { id: 1, title: 'Review resumes', description: 'Review all received resumes.' },
        { id: 2, title: 'Schedule interviews', description: 'Schedule interviews with selected candidates.' },
        { id: 5, title: 'Conduct interviews', description: 'Interview candidates.' },
      ],
    },
    {
      id: 2,
      name: 'In Progress',
      tasks: [
        { id: 3, title: 'Conduct interviews', description: 'Interview candidates.' },
      ],
    },
    {
      id: 3,
      name: 'Done',
      tasks: [
        { id: 4, title: 'Send offers', description: 'Send job offers to selected candidates.' },
      ],
    },
  ],
};

const DraggableCard = ({ task, index, stageId, moveCard, openModal, setDraggingId, draggingId }) => {
  const ref = useRef(null);

  const [{ isDragging }, drag] = useDrag({
    type: ItemTypes.CARD,
    item: { ...task, index, stageId },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
    end: () => {
      setDraggingId(null);
    }
  });

  const [, drop] = useDrop({
    accept: ItemTypes.CARD,
    hover(draggedItem, monitor) {
      if (!ref.current) return;
      const hoverBoundingRect = ref.current.getBoundingClientRect();
      const hoverMiddleY = (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
      const clientOffset = monitor.getClientOffset();
      const hoverClientY = clientOffset.y - hoverBoundingRect.top;

      if (draggedItem.stageId === stageId && draggedItem.index !== index) {
        if (draggedItem.index < index && hoverClientY > hoverMiddleY) {
          moveCard(draggedItem.id, stageId, index);
          draggedItem.index = index;
        } else if (draggedItem.index > index && hoverClientY < hoverMiddleY) {
          moveCard(draggedItem.id, stageId, index);
          draggedItem.index = index;
        }
      } else if (draggedItem.stageId !== stageId) {
        moveCard(draggedItem.id, stageId, index);
        draggedItem.stageId = stageId;
        draggedItem.index = index;
      }
    },
  });

  drag(drop(ref));

  return (
    <div
      ref={ref}
      className={`task ${isDragging ? 'dragging' : ''} ${draggingId === task.id ? 'preview' : ''}`}
      style={{ opacity: isDragging ? 0.5 : 1 }}
      onMouseDown={() => setDraggingId(task.id)}
    >
      <Card onClick={() => openModal(task)} className={isDragging ? 'dragging-card' : ''}>
        <CardContent>
          <Typography variant="h6">{task.title}</Typography>
          <Typography variant="body2">{task.description}</Typography>
        </CardContent>
      </Card>
    </div>
  );
};

const Stage = ({ stage, moveCard, openModal, addTask, setDraggingId, draggingId }) => {
  const [, drop] = useDrop({
    accept: ItemTypes.CARD,
    hover(draggedItem) {
      if (draggedItem.stageId !== stage.id) {
        moveCard(draggedItem.id, stage.id, stage.tasks.length);
        draggedItem.stageId = stage.id;
        draggedItem.index = stage.tasks.length;
      }
    },
  });

  return (
    <Box ref={drop} className="stage">
      <Typography variant="h6" align="center" sx={{ mb: 2 }}>{stage.name}</Typography>
      <Button
        variant="outlined"
        startIcon={<AddIcon />}
        sx={{ mb: 2, width: '100%' }}
        onClick={() => addTask(stage.id)}
      >
        Add Task
      </Button>
      {stage.tasks.map((task, index) => (
        <DraggableCard
          key={task.id}
          task={task}
          index={index}
          stageId={stage.id}
          moveCard={moveCard}
          openModal={openModal}
          setDraggingId={setDraggingId}
          draggingId={draggingId}
        />
      ))}
    </Box>
  );
};

const BoardPage = () => {
  const [board, setBoard] = useState(fakeBoardData);
  const [open, setOpen] = useState(false);
  const [currentTask, setCurrentTask] = useState(null);
  const [isAddingStage, setIsAddingStage] = useState(false);
  const [draggingId, setDraggingId] = useState(null);

  useEffect(() => {
    setBoard((prevBoard) => ({ ...prevBoard })); // Emulate an initial setBoard call
  }, []);

  const findCard = (id) => {
    for (const stage of board.stages) {
      const taskIndex = stage.tasks.findIndex((task) => task.id === id);
      if (taskIndex > -1) {
        return {
          stageId: stage.id,
          task: stage.tasks[taskIndex],
          index: taskIndex,
        };
      }
    }
    return { stageId: null, task: null, index: -1 };
  };

  const moveCard = (id, toStageId, toIndex) => {
    const { stageId: fromStageId, task, index: fromIndex } = findCard(id);
    if (fromStageId === null || toStageId === null || task === null) return;

    const fromStage = board.stages.find((stage) => stage.id === fromStageId);
    const toStage = board.stages.find((stage) => stage.id === toStageId);

    if (fromStage === undefined || toStage === undefined) return;

    fromStage.tasks.splice(fromIndex, 1);
    toStage.tasks.splice(toIndex, 0, task);

    setBoard((prevBoard) => ({
      ...prevBoard,
      stages: prevBoard.stages.map((stage) =>
        stage.id === fromStageId || stage.id === toStageId ? { ...stage } : stage
      ),
    }));
  };

  const openModal = (task) => {
    setCurrentTask(task);
    setOpen(true);
  };

  const closeModal = () => {
    setOpen(false);
    setCurrentTask(null);
  };

  const handleEditTask = (e) => {
    const { name, value } = e.target;
    setCurrentTask({ ...currentTask, [name]: value });
  };

  const saveTask = () => {
    const updatedStages = board.stages.map((stage) => {
      const tasks = stage.tasks.map((task) =>
        task.id === currentTask.id ? currentTask : task
      );
      return { ...stage, tasks };
    });

    setBoard((prevBoard) => ({ ...prevBoard, stages: updatedStages }));
    closeModal();
  };

  const addTask = (stageId) => {
    const newTask = { id: Date.now(), title: 'New Task', description: 'Task description' };
    const updatedStages = board.stages.map((stage) =>
      stage.id === stageId ? { ...stage, tasks: [...stage.tasks, newTask] } : stage
    );
    setBoard((prevBoard) => ({ ...prevBoard, stages: updatedStages }));
  };

  const addStage = () => {
    const newStage = { id: Date.now(), name: 'New Stage', tasks: [] };
    setBoard((prevBoard) => ({ ...prevBoard, stages: [...prevBoard.stages, newStage] }));
    setIsAddingStage(false);
  };

  return (
    <DndProvider backend={HTML5Backend}>
      <Box sx={{ p: 3 }}>
        <Typography variant="h4" align="center" gutterBottom>{board.name}</Typography>
        <Typography variant="subtitle1" align="center" gutterBottom>{board.description}</Typography>
        <Box className="stages-container">
          {board.stages.map((stage) => (
            <Stage
              key={stage.id}
              stage={stage}
              moveCard={moveCard}
              openModal={openModal}
              addTask={addTask}
              setDraggingId={setDraggingId}
              draggingId={draggingId}
            />
          ))}
          <Box className="add-section">
            <Button
              variant="outlined"
              startIcon={<AddIcon />}
              sx={{ width: '300px', height: '100%' }}
              onClick={() => setIsAddingStage(true)}
            >
              Add Section
            </Button>
          </Box>
        </Box>
        <Dialog open={open} onClose={closeModal}>
          <DialogTitle>Edit Task</DialogTitle>
          <DialogContent>
            {currentTask && (
              <Box>
                <TextField
                  fullWidth
                  margin="dense"
                  label="Title"
                  name="title"
                  value={currentTask.title}
                  onChange={handleEditTask}
                />
                <TextField
                  fullWidth
                  margin="dense"
                  label="Description"
                  name="description"
                  value={currentTask.description}
                  onChange={handleEditTask}
                  multiline
                  rows={4}
                />
              </Box>
            )}
          </DialogContent>
          <DialogActions>
            <Button onClick={closeModal}>Cancel</Button>
            <Button onClick={saveTask} variant="contained" color="primary">Save</Button>
          </DialogActions>
        </Dialog>
        <Dialog open={isAddingStage} onClose={() => setIsAddingStage(false)}>
          <DialogTitle>Add New Stage</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              label="Stage Name"
              fullWidth
              variant="standard"
              onBlur={(e) => {
                const newStage = { id: Date.now(), name: e.target.value, tasks: [] };
                setBoard((prevBoard) => ({ ...prevBoard, stages: [...prevBoard.stages, newStage] }));
                setIsAddingStage(false);
              }}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setIsAddingStage(false)}>Cancel</Button>
          </DialogActions>
        </Dialog>
      </Box>
    </DndProvider>
  );
};

export default BoardPage;
